import '../../cols.css'
import MultiGrid from '../util/MultiGrid'
import Image from "../util/Image"
import Ashes from "../../images/covers/Ashes Album Commission v03.png"
import BadEnergy from "../../images/covers/Bad Energy Album Commission.png"
import Chivok from "../../images/covers/Chivok Album Commission.png"
import DaredevilVid from "../../images/covers/Daredevil Album Animation (2).MP4"
import Daredevil from "../../images/covers/Daredevil Album Commission.png"
import ElDoradoVid from "../../images/covers/El Dorado Animation Insta Post.mp4"
import ElDoradoVid2 from "../../images/covers/El Dorado Animation Spotify.mp4"
import Yusdrew from "../../images/covers/RudyWade and Yusdrew Album Commission.png"
import DayOne from "../../images/covers/RudyWade Day One Album Commission.png"
import ShootingStar from "../../images/covers/RudyWade Shooting Star Album Commission.png"
import JackZick from "../../images/covers/RudyWade X JackZick Album Commission.png"
import Mistake from "../../images/covers/Sid Mistake Album Commission v02.png"
import MistakeVid from "../../images/covers/Sid Mistake Animation v02 spotify-insta.mp4"

const Covers = () => {
    const row1 = [
        <Image image={DayOne} />,
        <video autoPlay loop muted>
            <source src={ElDoradoVid2} type='video/mp4' />
        </video>,
        <video autoPlay loop muted>
            <source src={ElDoradoVid} type='video/mp4' />
        </video>,
    ]
    const row2 = [
        <video autoPlay loop muted>
            <source src={DaredevilVid} type='video/mp4' />
        </video>,
        <Image image={Daredevil} />,
        <Image image={Ashes} />,
    ]
    const row3 = [
        <Image image={JackZick} />,
        <Image image={Chivok} />,
        <Image image={BadEnergy} />,
    ]
    const row4 = [
        <Image image={Yusdrew} />,
        <Image image={ShootingStar} />,
        <Image image={Mistake} />,
        <video autoPlay loop muted>
            <source src={MistakeVid} type='video/mp4' />
        </video>,
    ]

    return (
        <div className="font-body text-xl container mx-auto flex flex-wrap ">
            <MultiGrid items={row1} numCols={3} />
            <div className="w-full flex">
                <div className="ml-2 w-1/3">
                Day One - RudyWade
                </div>
                <div className="mr-5 text-right w-2/3">
                El Dorado - Stellar
                </div>
            </div>
            <MultiGrid items={row2} numCols={3} />
            <div className="w-full flex">
                <div className="ml-2 w-2/3">
                Daredevil - Stellar
                </div>
                <div className="mr-5 text-right w-1/3">
                Ashes (Martin Jenson Remix) - Stellar
                </div>
            </div>
            <MultiGrid items={row3} numCols={3} />
            <div className="w-full flex">
                <div className="ml-2 w-1/3">
                RudyWade X Jack Zick
                </div>
                <div className="w-1/3">
                Chivy - Chivok
                </div>
                <div className="mr-5 text-right w-1/3">
                Bad Energy - RudyWade
                </div>
            </div>
            <MultiGrid items={row4} numCols={4} />
            <div className="w-full flex">
                <div className="ml-2 w-1/4">
                Moving On - RudyWade
                </div>
                <div className="w-1/4">
                Shooting Star - RudyWade
                </div>
                <div className="text-right mr-5 w-2/4">
                Mistake - Stellar
                </div>
            </div>

        </div>
    )
}

export default Covers