import '../../cols.css'
import ImageGrid from '../util/ImageGrid'
import Image from '../util/Image'
import CharLineup from "../../images/space/Character Lineup.png"
import Captain from "../../images/space/Captain Promo.png"
import CaptainExpression from "../../images/space/Captain_Expressions.png"
import CaptainInitial from "../../images/space/Captain_InitialDesigns.png"
import CaptainModel from "../../images/space/Captain_ModelSheet.png"
import CaptainMovement from "../../images/space/Captain_MovementStudies.png"
import Gunner from "../../images/space/Gunner promo.png"
import GunnerExpression from "../../images/space/Gunner_Expressions.png"
import GunnerInitial from "../../images/space/Gunner_Inital Designs.png"
import GunnerModel from "../../images/space/Gunner_ModelSheet.png"
import GunnerMovement from "../../images/space/Gunner_MovementStudies.png"
import PowderMonkey from "../../images/space/Powder Monkey Promo.png"
import PowderMonkeyExpression from "../../images/space/PowderMonkey_Expressions.png"
import PowderMonkeyInitial from "../../images/space/PowderMonkey_InitialDesigns.png"
import PowderMonkeyModel from "../../images/space/PowderMonkey_ModelSheet.png"
import PowderMonkeyMovement from "../../images/space/PowderMonkey_MovementStudies.png"
import Quarter from "../../images/space/Quartermaster Promo.png"
import QuarterInitial from "../../images/space/QuarterMaster_InitalDesigns.png"
import QuarterModel from "../../images/space/QuarterMaster_ModelSheet.png"
import QuarterModel2 from "../../images/space/QuarterMaster_ModelSheet02.png"
import QuarterMovement from "../../images/space/QuarterMaster_MovementStudies.png"
import QuarterWeapon from "../../images/space/QuarterMaster_WeaponDesign.png"
import Striker from "../../images/space/Striker Promo.png"
import StrikerExpression from "../../images/space/Striker_Expressions.png"
import StrikerInitial from "../../images/space/Striker_InitialDesigns.png"
import StrikerModel from "../../images/space/Striker_ModelSheet.png"
import StrikerMovement from "../../images/space/Striker_MovementStudies.png"
import Surgeon from "../../images/space/Surgeon Promo.png"
import SurgeonExpression from "../../images/space/Surgeon_Expressions.png"
import SurgeonExpressionLow from "../../images/space/Surgeon_Expressions low.png"
import SurgeonInitial from "../../images/space/Surgeon_InitalDesign.png"
import SurgeonModelLow from "../../images/space/Surgeon_ModelSheet low.png"
import SurgeonMovement from "../../images/space/Surgeon_MovementStudies.png"


const Space = () => {
    const gunnerGrid = [
        GunnerMovement,
        GunnerExpression,
        GunnerModel,
        GunnerInitial
    ]

    const surgeonGrid = [
        SurgeonModelLow,
        SurgeonMovement,
        SurgeonExpressionLow,
        SurgeonInitial,
    ]

    const quartermasterGrid = [
        QuarterModel2,
        QuarterInitial,
        QuarterWeapon,
        QuarterMovement,
    ]

    const powderMonkeyGrid = [
        PowderMonkeyModel,
        PowderMonkeyMovement,
        PowderMonkeyInitial,
        PowderMonkeyExpression,
    ]

    const strikerGrid = [
        StrikerMovement,
        StrikerInitial,
        StrikerExpression,
        StrikerModel,
    ]

    const captainGrid = [
        CaptainMovement,
        CaptainInitial,
        CaptainExpression,
        CaptainModel,
    ]

    // function componentDidMount() {
    //   gunnerGrid.forEach((pic) => {
    //     const img = <Image image={pic} />;
    //   });
    // }

    return (
        <div className="container mx-auto flex flex-wrap ">
            <div className="w-1/2 mx-auto">
                <Image image={CharLineup} />
            </div>
            <div className="text-center mt-5 font-body text-l mx-auto">
                <p>
                    For this project I designed a group of six characters to be a crew of
                    space pirates! I had a blast designing these characters!
                </p>
            </div>
            <div className="flex mt-5  justify-center flex-wrap">
                <div className="my-auto mx-auto w-1/3">
                    <Image image={Gunner} />
                </div>
                <div className="my-auto mx-auto w-2/3">
                    <ImageGrid numCols={2} images={gunnerGrid} />
                </div>
            </div>
            <div className="flex mt-5  justify-center flex-wrap">
                <div className="my-auto mx-auto w-2/3">
                    <ImageGrid numCols={2} images={surgeonGrid} />
                </div>
                <div className="my-auto mx-auto w-1/3">
                    <Image image={Surgeon} />
                </div>
            </div>
            <div className="flex mt-5  justify-center flex-wrap">
                <div className="my-auto mx-auto w-1/3">
                    <Image image={Quarter} />
                    <div className="mt-5">
                        <Image image={QuarterModel} />
                    </div>
                </div>
                <div className="my-auto mx-auto w-2/3">
                    <ImageGrid numCols={2} images={quartermasterGrid} />
                </div>
            </div>
            <div className="flex mt-5  justify-center flex-wrap">
                <div className="my-auto mx-auto w-2/3">
                    <ImageGrid numCols={2} images={powderMonkeyGrid} />
                </div>
                <div className="my-auto mx-auto w-1/3">
                    <Image image={PowderMonkey} />
                </div>
            </div>
            <div className="flex mt-5  justify-center flex-wrap">
                <div className="my-auto mx-auto w-1/3">
                    <Image image={Striker} />
                </div>
                <div className="my-auto mx-auto w-2/3">
                    <ImageGrid numCols={2} images={strikerGrid} />
                </div>
            </div>
            <div className="flex mt-5  justify-center flex-wrap">
                <div className="my-auto mx-auto w-2/3">
                    <ImageGrid numCols={2} images={captainGrid} />
                </div>
                <div className="my-auto mx-auto w-1/3">
                    <Image image={Captain} />
                </div>
            </div>
        </div>
    )
}

export default Space