import '../../cols.css'
import ImageGrid from '../util/ImageGrid'
import CreepyDiagram from "../../images/mew/Creepy Forest Assets Diagram.png"
import CityOut from "../../images/mew/Meowtations City Outskirts.png"
import City from "../../images/mew/Meowtations City.png"
import CreepyForest from "../../images/mew/Meowtations Creepy Forest.png"
import Facility from "../../images/mew/Meowtations Facility.png"
import Cottage from "../../images/mew/Meowtations Mitten's cottage.png"
import Subway from "../../images/mew/Meowtations Subway.png"
import Splash from "../../images/mew/Mewtations Splash.png"
import Image from '../util/Image'

const Mewtations = () => {
    const grid1 = [Cottage,
                   CityOut,
                   Facility,
                   City,
                   CreepyForest,
                   CreepyDiagram,
                ]

    return (
        <div className="container mx-auto flex flex-wrap ">
            <div className="mx-auto w-4/6">
                <Image image={Splash} />
            </div>
            <div className="mx-auto text-xl text-center font-body w-1/2">
                <br />
                <p>
                    This was a short project where a small 
                    group of us made a 2D platformer game. I 
                    made six enviornment concepts as well as 
                    eight tileable 2D assests and a splash  
                    illustration.  We wanted to have different 
                    enviornments in game, but we only accomplished 
                    one enviornment and basic game play.
                </p>
                <br />
            </div>
            <div className="mx-auto w-4/6">
                <Image image={Subway} />
            </div>
            <ImageGrid images={grid1} numCols={2} />
        </div>
    )
}

export default Mewtations