import quad from "../../images/elm/IMG_0246.JPG"
import first from "../../images/elm/IMG_0299.JPG"
import second from "../../images/elm/IMG_0300.JPG"
import third from "../../images/elm/IMG_0301.JPG"
import fourth from "../../images/elm/IMG_0304.JPG"
import port2 from "../../images/elm/AGDII Portfolio 02.png"
import port3 from "../../images/elm/AGDII Portfolio 03.png"
import port4 from "../../images/elm/AGDII Portfolio 04.png"
import port5 from "../../images/elm/AGDII Portfolio 05.png"
import port6 from "../../images/elm/AGDII Portfolio 06.png"
import uifull from "../../images/elm/IMG_0241.JPG"
import uiempty from "../../images/elm/IMG_0243.JPG"
import menu from "../../images/elm/mock up 02.png"
import menu2 from "../../images/elm/mock up 03.png"
import splashVid from "../../images/elm/Elm Splash Animation.mp4"
import '../../cols.css'
import ImageGrid from '../util/ImageGrid'
import Image from "../util/Image"

const Elm = () => {
    const grid1 = [
        first,
        second,
        third,
        fourth,
    ]
    const grid2 = [
        port2,
        port3,
        port4,
        port5,
    ]
    const grid3 = [
        menu,
        menu2,
        uiempty,
        uifull,
    ]

    return (
      <div className="font-body text-xl container mx-auto h-full flex flex-wrap ">
        <div className="w-2/3 mx-auto">
          <p>
            I worked with a team to make this game in seven weeks! This game is
            stealth base where you avoid aliens and collect key parts in order
            to escape.
          </p>
          <p>
            You can find more info and download the Beta version
            <a target="_blank" href="https://stallout.wixsite.com/elm-game">
              {" "}
              HERE
            </a>
          </p>
          <p>
            The game has been shipped and you can find it
            <a target="_blank" href="https://stallout.itch.io/elm">
              {" "}
              HERE
            </a>
          </p>
          <p>
            3D Modeling done by
            <a target="_blank" href="https://coadler.com/">
              {" "}
              Christian Orion Adler{" "}
            </a>
            and
            <a target="_blank" href="https://nickparker.carbonmade.com">
              {" "}
              Nick Parker
            </a>
          </p>
          <br />
        </div>
        <div className="flex flex-row">
            <div className="mr-8 flex w-1/2">
                <Image image={quad} />
            </div>
            <div className="flex w-1/2">
                <ImageGrid images={grid1} numCols={2} />
            </div>
        </div>
        <div className="mt-5 mx-auto w-1/2">
          <video autoPlay loop muted>
            <source src={splashVid} type="video/mp4" />
          </video>
        </div>
        <ImageGrid images={grid2} numCols={2} />
        <div className="mt-5">
            <Image image={port6} />
        </div>
        <ImageGrid images={grid3} numCols={4} />
      </div>
    );
}

export default Elm
