import '../../cols.css'
import ImageGrid from '../util/ImageGrid'
import Image from '../util/Image'
import main from "../../images/woo/ISP Concepts Render 01.png"
import main2 from "../../images/woo/ISP Concepts Render 02.png"
import tunnel from "../../images/woo/ISP Concepts Render 04.png"
import fountain from "../../images/woo/ISP Concepts Render 03.png"
import stairs from "../../images/woo/ISP Concepts Render 08 low res.png"
import fountain2 from "../../images/woo/ISP Concepts Render 07.png"
import table from "../../images/woo/ISP Concepts Render 05.png"
import woods from "../../images/woo/ISP Concepts Render 06.png"
import greenhouse from "../../images/woo/ISP Concepts 01.png"
import circularbuilding from "../../images/woo/ISP Concepts 03.png"
import janelle from "../../images/woo/Janelle Slide.png"
import keirsten from "../../images/woo/Kiersten Slide.png"
import nataile from "../../images/woo/Natalie Slide.png"
import slide1 from "../../images/woo/Slide 03.png"
import slide2 from "../../images/woo/Slide 04.png"
import slide3 from "../../images/woo/Slide 5.png"
import slide4 from "../../images/woo/Slide 06.png"

const Worcester = () => {
    const grid1 = [
        tunnel,
        fountain,
        stairs,
        fountain2,
        table,
        woods,
    ]
    const grid2 = [
        greenhouse,
        circularbuilding,
    ]
    const grid3 = [
        janelle,
        keirsten,
        nataile,
    ]
    const grid4 = [
        slide1,
        slide2,
        slide3,
        slide4,
    ]
    const textBlurb = "This was a contest that me and two other ladies did together.  We were hoping to enter the Disney Imagineer contest, but they " +
                        "cancelled it for 2020. So we decided to do a previous years prompt which was all about story.  We as a team decided to make a " +
                        "botanical garden with topiary, telling the history of Worcster.  This is all the concept art I did for the project! Enjoy!"

    return (
        <div className="container mx-auto flex flex-wrap ">
            {/* <div>
                <div>
                    <Image image={main} />
                </div>
                <div>
                    Youtube Video
                </div>
            </div> */}
            <div className="mt-5 mb-5 text-center text-xl font-body">
                <p>
                    {textBlurb}
                </p>
            </div>
            <Image image={main2} />
            <ImageGrid numCols={2} images={grid1} />
            <ImageGrid numCols={2} images={grid2} />
            <ImageGrid numCols={3} images={grid3} />
            <ImageGrid numCols={4} images={grid4} />
        </div>
    )
}

export default Worcester